import _ from '../../utils/index.js'
import newsItem from '../../components/news-item.vue'
const newsCategory = [
  { id: 'gn', name: '国内' },
  { id: 'gj', name: '国际' },
  { id: 'cj', name: '财经' },
  { id: 'yl', name: '娱乐' },
  { id: 'js', name: '纪实' },
  { id: 'ty', name: '台湾' },
  { id: 'other', name: '综合' },
  { id: 'gn', name: '国内' },
  { id: 'gj', name: '国际' },
  { id: 'cj', name: '财经' },
  { id: 'yl', name: '娱乐' },
  { id: 'js', name: '纪实' },
  { id: 'ty', name: '台湾' },
  { id: 'other', name: '综合' }
]
export default {
	data() {
		return {
			title: '新闻快讯',
			newsList: [], // 当前新闻列表
			swiperList: [], // 当前banner列表
			curTypeIdx: 0, // 默认国内新闻
			newsCategory,
		}
	},
	components: {
		newsItem,
	},
	onLoad() {
		this.requestData()
	},
	onPullDownRefresh() {
		this.requestData(_ => {
			uni.stopPullDownRefresh();
		})
	},
	methods: {
		requestData(cb) {
			uni.request({
				url: 'https://test-miniprogram.com/api/news/list',
				method: 'GET',
				data: { type: newsCategory[this.curTypeIdx].id },
				success: res => {
					console.log(res)
					const { result } = res.data
					let newsList = result.map(item => {
						item.date = _.formatTime(item.date, 'HH:mm')
						return item
					})
					this.swiperList = newsList.slice(0, 3)
					this.newsList = newsList
				},
				fail: () => {},
				complete: () => {
					cb && cb()
				}
			});
		},
		onTapItem(e) {
			const newsId = e.currentTarget.dataset.newsId
			console.log(newsId)
			uni.navigateTo({
				url: '../info/info?newsId=' + newsId
			});
		},
		changeType(curTypeIdx) {
			if (curTypeIdx === this.curTypeIdx) return
			this.curTypeIdx = curTypeIdx
			this.requestData()
		}
	}
}
